/*--------------------------
      Global Styles
---------------------------*/
html,
body {
  overflow-x: hidden;
  min-height: 100vh;
  /* background-color: red; */
}

body {
  background-color: #ffffff;
  font-family: "Heebo", sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #51668a;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::selection {
  background-color: #ff5e14;
  color: #ffffff;
}

a {
  color: #ff5e14;
  -webkit-transition: color 0.3s ease;
  -moz-transition: color 0.3s ease;
  -ms-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

a:hover {
  color: #ad3600;
  text-decoration: none;
}

section {
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
}

img {
  max-width: 100%;
}

/*-------------------------
     RESET Default Styles
 --------------------------*/
*,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li {
  margin: 0;
  padding: 0;
}

*,
:active,
:focus {
  outline: none;
}

ul,
ol {
  list-style: none;
}

button {
  border: none;
}

button,
button:focus,
.btn.focus,
.btn:focus,
.form-control,
.form-control:focus {
  outline: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  -ms-box-shadow: none;
  -o-box-shadow: none;
  box-shadow: none;
}

textarea {
  resize: none;
}

select {
  background-color: transparent;
}

