}

.form-group {
  position: relative;
  margin-bottom: 30px;
}

.form-control {
  height: 60px;
  line-height: 60px;
  border: none;
  border: 2px solid #e7ebef;
  background-color: transparent;
  border-radius: 2px;
  padding: 0 0 0 20px;
  font-size: 14px;
}

.form-control:focus {
  background-color: transparent;
  border-color: #ff5e14;
}

textarea.form-control {
  height: 110px;
  padding-top: 10px;
  line-height: 25px;
}

.form-control::-webkit-input-placeholder {
  color: #51668a;
}

.form-control:-moz-placeholder {
  color: #51668a;
}

.form-control::-moz-placeholder {
  color: #51668a;
}

.form-control:-ms-input-placeholder {
  color: #51668a;
}

.form__title {
  font-size: 18px;
  line-height: 1;
  margin-bottom: 20px;
}

.form-group-select:after {
  content: "";
  font-family: FontAwesome;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.form-group-select select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  color: #51668a;
  cursor: pointer;
}

/* Input Radio */
.label-radio {
  display: block;
  position: relative;
  padding-left: 26px;
  margin-bottom: 0;
  cursor: pointer;
  font-size: 13px;
  font-weight: 400;
  color: #51668a;
}

.label-radio input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.radio-indicator {
  position: absolute;
  top: -1px;
  left: 0;
  height: 17px;
  width: 17px;
  background: transparent;
  border: 2px solid #eaeaea;
  border-radius: 50%;
}

.label-radio input:checked ~ .radio-indicator {
  background: transparent;
}

.label-radio:hover input:not([disabled]):checked ~ .radio-indicator,
.label-radio input:checked:focus ~ .radio-indicator {
  background: transparent;
}

.radio-indicator:after {
  content: "";
  position: absolute;
  display: none;
  left: 3px;
  top: 3px;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background: #ff5e14;
}

.label-radio input:checked ~ .radio-indicator:after {
  display: block;
}

